
html,body,ul,li,p{
	padding:0;
	margin:0;
	font-family: PingFangSC-Regular,Arial,"Helvetica Neue",Helvetica,sans-serif;
}
html,body{
	background:#f5f5f5;
}
li{
	list-style:none;
}
.logo_top{
  height:50px;
  margin-bottom:10px;
}
.tab{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	background:#fff;
	position:fixed;
	width:100%;
	height:50px;
	bottom:0;
	left:0;
	box-shadow: 0 -3px 4px rgba(0,0,0,.03);
}
.tab_item{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	color:#656565;
	text-decoration:none;
	font-size:12px;
}
.tab_item .icon{
	width:22px;
	height:22px;
	margin:6px auto 3px;
}
.tab_item .icon img{
	width:100%;
	height:100%;
}
.tab_item .tab_name{
	color:#656565;
	text-align:center;
}
.tab_bottom{
	height:50px;
	margin-top:10px;
}



